import React from 'react';
import styles from './Cart.module.css';
import ProductBlockInCart from '../../components/ProductBlockInCart';
import { useSelector, useDispatch } from 'react-redux';
import { clearCart, deleteItemFromCart, makeAPurchase } from '../../store/actions/cart';
import { Link } from 'react-router-dom';

import emptyCart from '../../assets/images/empty_cart.png';

const Cart = () => {
	const { items, totalPrice, totalCount } = useSelector(({ cart }) => cart);
	const dispatch = useDispatch();
	const onClearCart = () => {
		if (window.confirm('Вы действительно хотите очистить корзину?')) {
			dispatch(clearCart());
		}
	};
	const deleteItem = (number) => {
		dispatch(deleteItemFromCart(number));
	};

	const purchase = () => {
		console.log('Ваши покупки:', items);
		console.log('Их стоимость:', totalPrice);
		alert('Спасибо за покупку');
		dispatch(makeAPurchase());
		dispatch(clearCart());
	};
	return (
		<div className={styles.cart}>
			<div className={styles.container}>
				{totalCount ? (
					<div>
						<div className={styles.header}>
							<div className={styles.name_cart}>
								<svg
									width='18'
									height='18'
									viewBox='0 0 18 18'
									fill='none'
									xmlns='http://www.w3.org/2000/svg'
								>
									<path
										d='M6.33333 16.3333C7.06971 16.3333 7.66667 15.7364 7.66667 15C7.66667 14.2636 7.06971 13.6667 6.33333 13.6667C5.59695 13.6667 5 14.2636 5 15C5 15.7364 5.59695 16.3333 6.33333 16.3333Z'
										stroke='black'
										strokeWidth='1.8'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
									<path
										d='M14.3333 16.3333C15.0697 16.3333 15.6667 15.7364 15.6667 15C15.6667 14.2636 15.0697 13.6667 14.3333 13.6667C13.597 13.6667 13 14.2636 13 15C13 15.7364 13.597 16.3333 14.3333 16.3333Z'
										stroke='black'
										strokeWidth='1.8'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
									<path
										d='M4.78002 4.99999H16.3334L15.2134 10.5933C15.1524 10.9003 14.9854 11.176 14.7417 11.3722C14.4979 11.5684 14.1929 11.6727 13.88 11.6667H6.83335C6.50781 11.6694 6.1925 11.553 5.94689 11.3393C5.70128 11.1256 5.54233 10.8295 5.50002 10.5067L4.48669 2.82666C4.44466 2.50615 4.28764 2.21182 4.04482 1.99844C3.80201 1.78505 3.48994 1.66715 3.16669 1.66666H1.66669'
										stroke='black'
										strokeWidth='1.8'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
								</svg>
								<b> Корзина</b>
							</div>
							<div onClick={onClearCart} className={styles.clear}>
								<svg
									width='20'
									height='20'
									viewBox='0 0 20 20'
									fill='none'
									xmlns='http://www.w3.org/2000/svg'
								>
									<path
										d='M2.5 5H4.16667H17.5'
										stroke='#B6B6B6'
										strokeWidth='1.2'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
									<path
										d='M6.66663 5.00001V3.33334C6.66663 2.89131 6.84222 2.46739 7.15478 2.15483C7.46734 1.84227 7.89127 1.66667 8.33329 1.66667H11.6666C12.1087 1.66667 12.5326 1.84227 12.8451 2.15483C13.1577 2.46739 13.3333 2.89131 13.3333 3.33334V5.00001M15.8333 5.00001V16.6667C15.8333 17.1087 15.6577 17.5326 15.3451 17.8452C15.0326 18.1577 14.6087 18.3333 14.1666 18.3333H5.83329C5.39127 18.3333 4.96734 18.1577 4.65478 17.8452C4.34222 17.5326 4.16663 17.1087 4.16663 16.6667V5.00001H15.8333Z'
										stroke='#B6B6B6'
										strokeWidth='1.2'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
									<path
										d='M8.33337 9.16667V14.1667'
										stroke='#B6B6B6'
										strokeWidth='1.2'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
									<path
										d='M11.6666 9.16667V14.1667'
										stroke='#B6B6B6'
										strokeWidth='1.2'
										strokeLinecap='round'
										strokeLinejoin='round'
									/>
								</svg>

								<span> Очистить корзину</span>
							</div>
						</div>
						<div className={styles.productBlocks}>
							{items.map((item, i) => (
								<ProductBlockInCart
									key={`cart_item_${item.id}_${i}`}
									{...item}
									number={i}
									deleteItem={deleteItem}
								/>
							))}
						</div>
						<div className={styles.cartFooter}>
							<div className={styles.part1}>
								<div className={styles.totalProducts}>
									<b>
										Всего товаров: <span>{totalCount} шт. </span>
									</b>
								</div>
								<div className={styles.sum}>
									<b>
										Сумма заказа: <span>{totalPrice} р</span>
									</b>
								</div>
							</div>
							<div className={styles.part2}>
								<Link to='/shop/all'>
									<button className={styles.back}>Вернуться назад</button>
								</Link>

								<Link to='/shop/all'>
									<button onClick={purchase} className={styles.pay}>
										Оплатить сейчас
									</button>
								</Link>
							</div>
						</div>
					</div>
				) : (
					<div className={styles.emptyCart}>
						<h2>
							Корзина пуста <i>😕</i>
						</h2>
						<p>
							Вероятней всего, вы ещё ничего не выбрали.
							<br />
						</p>
						<img src={emptyCart} alt='Empty cart' />
						<Link to='/shop/all' className={styles.goBack}>
							<span>Вернуться назад</span>
						</Link>
					</div>
				)}
			</div>
		</div>
	);
};

export default Cart;
